<template>
	<view class="user-common">
		<view class="c-wrap">
			<view class="ch-box">
				<image src="../../static/bg.png" mode="" class="user-bg"></image>
				<view class="cb-num">
					<text class="balance-num">{{ wallet }}</text>
					元
				</view>
				<view class="balance-text">红包余额</view>
			</view>
		</view>
		<view class="balance-list">
			<template v-if="list.length > 0">
				<view class="box row verCenter bothSide" v-for="(item, index) in list" :key="index">
					<view class="column">
						<text class="t1">{{ item.msg }}</text>
						<text class="t2">{{ item.ctime_time }}</text>
					</view>
					<view>
						<text class="t3">{{ item.money }}</text>
					</view>
				</view>
			</template>
			<template v-else>
				<view class="no-date column rowCenter" style="margin-top: 200rpx;">
					<view class="bg"></view>
					<text>暂无交易记录~</text>
				</view>
			</template>
			<uni-load-more :status="more" v-if="list.length >= limit"></uni-load-more>
		</view>
		<view class="btn">红包充值</view>
	</view>
</template>

<script>
import { API } from '@/util/api.js';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';

export default {
	data() {
		return {
			walletDetail: {},
			wallet:'0.00',
			more: 'more',
			page: 1,
			flag: false,
			list: [],
			limit:10
		};
	},
	onShow() {
		this.getData();
	},
	onReachBottom() {
		if (this.flag) return;
		this.page++;
		this.getData();
	},
	methods: {
		getData() {
			this.more = 'loading';
			this.request(API.walletDetail, 'GET', { limit: this.limit, page: this.page },true).then(res => {
				this.wallet=res.data.wallet
				if (res.code === 10000) {
					if (res.data.walletLog.length > 0) {
						this.walletDetail = res.data;
						this.list = this.list.concat(res.data.walletLog);
					} else {
						this.flag = true;
						this.more = 'noMore';
					}
				}
			});
		}
	},
	components: { uniLoadMore }
};
</script>

<style lang="scss">
@import '../../assets/css/user/userCommon.scss';
</style>
